
import Component from 'vue-class-component'
import Vue from 'vue'
import { Getter } from 'vuex-class'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggleMap } from '@/modules/feature-toggles/store/state'
import { Prop } from 'vue-property-decorator'
import Icon from 'common/Icon.vue'
import { PromotionQualityTier, PromotionType } from '../../api/dtos'
import { promotions } from '../../store/namespace'
import { GET_PROMOTION_QUALITY_TIERS_GETTER } from '../../store/getters'
import { arePromotionVisibilityRulesEnabled } from '../../helpers'
import { PromotionQualityTiersListVM } from '../../models'

@Component({ name: 'PromotionTypeDisplay', components: { Icon } })
export default class PromotionTypeDisplay extends Vue {
  private readonly PromotionType = PromotionType
  @Prop({ required: true }) promotionType!: PromotionType
  @Prop({ required: true }) promotionQualityTier!: PromotionQualityTier
  @Prop() promotionPercentage!: string

  @Getter(GET_TOGGLES_GETTER) toggles!: FeatureToggleMap

  @promotions.Getter(GET_PROMOTION_QUALITY_TIERS_GETTER)
  promotionQualityTiers!: PromotionQualityTiersListVM | null

  get shouldDisplayPromoQuality() {
    return (
      arePromotionVisibilityRulesEnabled(this.toggles) &&
      !!this.promoQualityTier
    )
  }

  get promoQualityTier() {
    return this.promotionQualityTiers?.getTier(this.promotionQualityTier)
  }

  get promoQualityTierLabel() {
    return this.promoQualityTier?.label ?? ''
  }
}
