
import Component from 'vue-class-component'
import Vue from 'vue'
import { safeCall } from '@/modules/shared'
import { CreatePromotionButtonProvider } from '../create-promotion-buttons'
import GlovoBanner from '@/components/common/ui/GlovoBanner.vue'
import { Prop } from 'vue-property-decorator'
import { LayoutDirection } from '@/components/types'

const PROMOTIONS_BANNER_VISIBLE = 'promotions_banner_visible'

@Component({
  name: 'PromotionsBanner',
  components: { GlovoBanner, CreatePromotionButtonProvider }
})
export default class PromotionsBanner extends Vue {
  @Prop({ default: 'horizontal' }) layout!: LayoutDirection
  @Prop({ default: false }) dismissible!: boolean // false
  private isBannerVisible = true

  mounted() {
    this.isBannerVisible = this.getBannerVisibleValue()
  }

  getBannerVisibleValue() {
    return (
      !this.dismissible ||
      safeCall(() =>
        JSON.parse(this.$localStorage.get(PROMOTIONS_BANNER_VISIBLE))
      ) !== false
    )
  }

  handleCloseBannerClick() {
    if (this.dismissible) {
      this.isBannerVisible = false
      this.$localStorage.set(PROMOTIONS_BANNER_VISIBLE, this.isBannerVisible)
    }
  }
}
