
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'

import { getImgSrc } from '@/modules/shared'
import GlovoBanner from '@/components/common/ui/GlovoBanner.vue'

import { GlovoModal } from 'common/ui/modal'
import { CreatePromotionButtonLink } from '../create-promotion-buttons'
import { registerPromoTypesService } from '../../services/register-promo-types'

@Component({
  name: 'CreatePromotionDialog',
  components: { GlovoBanner, CreatePromotionButtonLink, GlovoModal }
})
export default class CreatePromotionDialog extends Vue {
  @Prop({ default: false }) value!: boolean

  get orderedPromotionsConfigs() {
    return registerPromoTypesService.orderedPromotionsConfigs
  }

  get getImgSrc() {
    return getImgSrc
  }
}
