
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { GlovoTooltip } from 'common/ui/tooltip'
import { GlovoLink } from 'common/ui/link'
import { GlovoModal } from 'common/ui/modal'
import DetailsSection from '../details-section/DetailsSection.vue'
import { PromotionDetails } from './PromotionDetailsDisplay.types'

@Component({
  name: 'PromotionDetailsDisplay',
  components: { DetailsSection, GlovoLink, GlovoTooltip, GlovoModal }
})
export default class PromotionDetailsDisplay extends Vue {
  private tooltipTriggerRef: unknown = null
  private modalIsVisible = false

  @Prop({ required: true }) detailsDisplayInfo!: PromotionDetails
  @Prop({ required: true }) linkTitle!: string
  @Prop({ required: true }) modalTitle!: string

  get hasThreeItemsOrLess() {
    return this.detailsDisplayInfo.totalCount <= 3
  }

  mounted() {
    this.tooltipTriggerRef = this.$refs.subItemLink
  }

  handleClick() {
    this.modalIsVisible = !this.hasThreeItemsOrLess
  }
}
