
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import GlovoBadge from 'common/ui/GlovoBadge.vue'
import { GlovoTooltip } from 'common/ui/tooltip'
import { PromotionId } from '../../api/dtos'

@Component({
  name: 'PromotionNameDisplay',
  components: { GlovoTooltip, GlovoBadge }
})
export default class PromotionNameDisplay extends Vue {
  private tooltipTriggerRef: unknown = null
  @Prop({ required: true }) promotionName!: string
  @Prop({ required: true }) promotionId!: PromotionId

  mounted() {
    this.tooltipTriggerRef = this.$refs.promoName
  }

  get mouseOverDelayMillis() {
    return 400
  }
}
