import currencyService from '@/services/currency/CurrencyService'
import { Promotion, PromotionsList } from '../../models'
import {
  PromotionColumn,
  PromotionRow,
  PROMOTIONS_COLUMNS
} from '../constants/promotions-table-data'

const COLUMNS_WITH_CURRENCY: PromotionColumn['name'][] = [
  'budget',
  'currentSpend'
]

const formatColumnLabel = (
  columnName: PromotionColumn['name'],
  columnLabel: PromotionColumn['label']
) => {
  if (COLUMNS_WITH_CURRENCY.includes(columnName)) {
    return `${columnLabel} (${currencyService.getCurrencySymbol()})`
  }

  return columnLabel
}

export const mapPromotionsTableColumns = ($t: Vue['$t']): PromotionColumn[] => {
  return PROMOTIONS_COLUMNS.map(({ label, ...restColumns }) => ({
    ...restColumns,
    label: formatColumnLabel(
      restColumns.name,
      ($t(label) as string).toUpperCase()
    )
  }))
}

export const mapPromotionsToTableRows = (
  promotionsList: PromotionsList
): PromotionRow[] => {
  const { promotions = [] } = promotionsList || {}
  return (promotions || [])
    .filter(promotion => !!promotion)
    .map(promotion => mapPromotionToTableRow(promotion))
}

const mapPromotionToTableRow = (promotion: Promotion): PromotionRow => {
  const {
    id,
    budget,
    spendingPercentage,
    cityCode,
    spendingSummary,
    startDate,
    endDate
  } = promotion

  const formattedSpendingPercentage = spendingPercentage
    ? ` (${spendingPercentage})`
    : spendingPercentage

  return {
    id,
    name: promotion,
    type: promotion,
    city: cityCode,
    storeAddress: promotion,
    dates: `${startDate} ${endDate}`,
    products: promotion,
    budget: budget.amount || '-',
    currentSpend: spendingSummary.currentSpend
      ? spendingSummary.currentSpend + formattedSpendingPercentage
      : '-',
    status: promotion
  }
}
