
import Vue from 'vue'
import Component from 'vue-class-component'
import CreatePromotionDialog from '../create-promotion-dialog/CreatePromotionDialog.vue'
import CreatePromotionButton from './CreatePromotionButton.vue'
import { Watch } from 'vue-property-decorator'

@Component({
  name: 'CreatePromotionDialogButton',
  components: { CreatePromotionDialog, CreatePromotionButton }
})
export default class CreatePromotionDialogButton extends Vue {
  modalIsVisible = false

  created() {
    const { promotionSelectorOpened } = this.$route.query
    if (promotionSelectorOpened) {
      this.openModal()
    }
  }

  @Watch('modalIsVisible')
  onModalVisibilityChange() {
    const { promotionSelectorOpened } = this.$route.query

    if (!this.modalIsVisible && promotionSelectorOpened) {
      this.setPromotionSelectorQueryParam(false)
    } else if (this.modalIsVisible && !promotionSelectorOpened) {
      this.setPromotionSelectorQueryParam(true)
    }
  }

  private setPromotionSelectorQueryParam(value: boolean) {
    this.$router.replace({
      query: {
        ...this.$route.query,
        promotionSelectorOpened: value ? 'true' : undefined
      }
    })
  }

  handleCreateButtonClick() {
    this.openModal()
  }

  private openModal() {
    this.modalIsVisible = true
  }
}
