
import Component from 'vue-class-component'
import Vue from 'vue'
import { Watch } from 'vue-property-decorator'

import EmptyPromotionsList from './EmptyPromotionsList.vue'
import {
  authentication,
  GET_SELECTED_STORE_ADDRESS_IDS_GETTER
} from '@/modules/authentication'
import { StoreAddressId } from '@/modules/store/data/resources'
import GlovoTable from 'common/ui/GlovoTable.vue'
import { CreatePromotionButtonProvider } from '../create-promotion-buttons'
import PromotionsBanner from '../promotions-banner/PromotionsBanner.vue'
import GlovoErrorPage from 'common/ui/GlovoErrorPage.vue'
import { PromotionsList as PromotionsListVM } from '../../models'
import { promotions } from '../../store/namespace'
import {
  GET_PROMOTIONS,
  GetPromotionsFailurePayload
} from '../../store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import { GetPromotionsPayload } from '../../api/promotions'
import {
  GET_PROMOTIONS_ERROR_GETTER,
  GET_PROMOTIONS_GETTER
} from '../../store/getters'
import PromotionStoreDetails from '../promotions-store-details/PromotionStoreDetails.vue'
import PromotionListStatusDisplay from './status-display/PromotionListStatusDisplay.vue'
import PromotionTypeDisplay from '../promotion-type-display/PromotionTypeDisplay.vue'
import PromotionNameDisplay from '../promotion-name-display/PromotionNameDisplay.vue'
import PromotionProductsDetails from '@/modules/promotions/components/promotions-products-details/PromotionProductsDetails.vue'
import PromotionsListTable from '@/modules/promotions/components/promotions-list/PromotionsListTable.vue'

@Component({
  name: 'PromotionsList',
  components: {
    PromotionsListTable,
    PromotionProductsDetails,
    EmptyPromotionsList,
    GlovoTable,
    CreatePromotionButtonProvider,
    PromotionsBanner,
    GlovoErrorPage,
    PromotionListStatusDisplay,
    PromotionStoreDetails,
    PromotionTypeDisplay,
    PromotionNameDisplay
  }
})
export default class PromotionsList extends Vue {
  @authentication.Getter(GET_SELECTED_STORE_ADDRESS_IDS_GETTER)
  selectedStoreAddressIds!: StoreAddressId[]

  @promotions.Action(GET_PROMOTIONS) getPromotions!: StoreAction<
    GetPromotionsPayload
  >

  @promotions.Getter(GET_PROMOTIONS_GETTER) promotionsList!: PromotionsListVM

  @promotions.Getter(GET_PROMOTIONS_ERROR_GETTER)
  promotionsGetApiError!: GetPromotionsFailurePayload

  @Watch('selectedStoreAddressIds')
  onSelectedStoreAddressesChange() {
    this.loadPromotions()
  }

  mounted() {
    this.loadPromotions()
  }

  get promotionListHasPromotions() {
    return this.promotionsList && this.promotionsList.hasPromotions
  }

  loadPromotions() {
    this.getPromotions({ storeAddressIds: this.selectedStoreAddressIds })
  }

  handleRetryAction() {
    location.reload()
  }

  handlePromotionUpdateError(errorMessage: string) {
    this.$toast.show({ message: errorMessage })
    this.loadPromotions()
  }
}
