
import Vue from 'vue'
import Component from 'vue-class-component'
import EmptyPromotionsList from './EmptyPromotionsList.vue'
import GlovoTable from 'common/ui/GlovoTable.vue'
import PromotionListStatusDisplay from './status-display/PromotionListStatusDisplay.vue'
import PromotionNameDisplay from '../promotion-name-display/PromotionNameDisplay.vue'
import PromotionProductsDetails from '@/modules/promotions/components/promotions-products-details/PromotionProductsDetails.vue'
import PromotionStoreDetails from '../promotions-store-details/PromotionStoreDetails.vue'
import PromotionTypeDisplay from '../promotion-type-display/PromotionTypeDisplay.vue'
import {
  PromotionColumn,
  PROMOTIONS_COLUMNS
} from '../constants/promotions-table-data'
import {
  mapPromotionsTableColumns,
  mapPromotionsToTableRows
} from './PromotionsList.helpers'
import { promotions } from '@/modules/promotions/store/namespace'
import {
  GET_PROMOTIONS_GETTER,
  NEWLY_CREATED_PROMOTION_ID_GETTER,
  PROMOTIONS_LOADING_GETTER
} from '@/modules/promotions/store/getters'
import { PromotionsList } from '@/modules/promotions/models'
import GlovoTableRowAnimationConfig from '@/components/common/ui/GlovoTableRowAnimationConfig'
import {
  UPDATE_NEWLY_CREATED_PROMOTION_ID,
  UpdateNewlyCreatedPromotionIdPayload
} from '../../store/action-types'
import { StoreAction } from '@/modules/store/types/store'

@Component({
  name: 'PromotionsListTable',
  components: {
    EmptyPromotionsList,
    GlovoTable,
    PromotionListStatusDisplay,
    PromotionNameDisplay,
    PromotionProductsDetails,
    PromotionStoreDetails,
    PromotionTypeDisplay
  }
})
export default class PromotionsListTable extends Vue {
  @promotions.Getter(PROMOTIONS_LOADING_GETTER)
  arePromotionsLoading!: boolean

  @promotions.Getter(GET_PROMOTIONS_GETTER) promotionsList!: PromotionsList

  @promotions.Getter(NEWLY_CREATED_PROMOTION_ID_GETTER)
  newlyCreatedPromotionId!: number

  @promotions.Action(UPDATE_NEWLY_CREATED_PROMOTION_ID)
  updateNewlyCreatedPromotionId!: StoreAction<
    UpdateNewlyCreatedPromotionIdPayload
  >

  get rows() {
    return this.arePromotionsLoading
      ? []
      : mapPromotionsToTableRows(this.promotionsList)
  }

  get columns() {
    return mapPromotionsTableColumns(this.$t)
  }

  get promosTableCssClass() {
    return this.rows.length > 0 ? 'with-data' : 'empty'
  }

  get rowToHighlightAnimationConfig(): GlovoTableRowAnimationConfig {
    return {
      rowId: this.newlyCreatedPromotionId,
      completion: () => this.updateNewlyCreatedPromotionId(null)
    }
  }

  get columnNames() {
    return PROMOTIONS_COLUMNS.map(({ name }) => name)
  }

  isTruncatedColumn(columnName: PromotionColumn['name']) {
    return this.truncatedColumnNames.includes(columnName)
  }

  get truncatedColumnNames(): PromotionColumn['name'][] {
    return ['storeAddress']
  }

  handleUpdatePromotionError(errorMessage: string) {
    this.$emit('promotion-update-error', errorMessage)
  }
}
