
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import PromotionDetailsDisplay from '../promotion-details-display/PromotionDetailsDisplay.vue'
import { PromotionDetails } from '../promotion-details-display/PromotionDetailsDisplay.types'
import { PromotionInfo } from '@/modules/promotions/models'

@Component({
  name: 'PromotionProductsDetails',
  components: { PromotionDetailsDisplay }
})
export default class PromotionProductsDetails extends Vue {
  @Prop({ required: true }) promotionInfo!: PromotionInfo

  get detailsLinkText() {
    const { totalProductsCount, storesInPromotion } = this.promotionInfo
    switch (totalProductsCount) {
      case 0:
        return this.$t('promotions.products.all.text')
      case 1:
        return storesInPromotion[0].products[0]
      default:
        return this.$t('promotions.products.multiple.text')
    }
  }

  get titleForDetailsModal() {
    return this.$t('promotions.products.count.text', {
      productsCount: this.promotionInfo.totalProductsCount
    }) as string
  }

  get detailsDisplayInfo(): PromotionDetails {
    const { totalProductsCount, storesInPromotion } = this.promotionInfo
    return {
      totalCount: totalProductsCount,
      items: storesInPromotion.map(store => ({
        name: store.storeName,
        subItems: this.isAtLeastOneProductIn(store.products)
          ? store.products
          : [this.$t('promotions.products.all-products.text') as string]
      }))
    }
  }

  private isAtLeastOneProductIn(products: string[] = []): boolean {
    return products.length > 0
  }
}
