
import Component from 'vue-class-component'
import Vue from 'vue'
import { Action, Getter } from 'vuex-class'
import { Watch } from 'vue-property-decorator'

import { DISPLAY_PROMOTIONS } from '@/modules/store/services/display-types'
import { ONE_SECOND_IN_MILLIS } from '@/modules/core/helpers'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggleMap } from '@/modules/feature-toggles/store/state'
import { authentication } from '@/modules/authentication/store/namespace'
import { IS_STORE_ADMIN_LOADED_GETTER } from '@/modules/authentication/store/getters'
import { StoreAction } from '@/modules/store/types/store'
import { ToBeMigrated } from 'types/any'
import { registerPromoTypesService } from '../../services/register-promo-types'
import {
  arePromotionsEnabled,
  arePromotionVisibilityRulesEnabled
} from '../../helpers'
import { promotions } from '../../store/namespace'
import { GET_PROMOTION_QUALITY_TIERS } from '../../store/action-types'
import { GET_PROMOTION_QUALITY_TIERS_GETTER } from '../../store/getters'
import { PromotionQualityTiersListVM } from '../../models'

@Component({ name: 'PromotionsContainer' })
export default class PromotionsContainer extends Vue {
  private promotionsContainerEnabled = false
  private promotionsViewTimeout!: NodeJS.Timeout

  @Action('displayState') displayState!: ToBeMigrated

  @Getter(GET_TOGGLES_GETTER) toggles!: FeatureToggleMap

  @promotions.Action(GET_PROMOTION_QUALITY_TIERS)
  getPromotionQualityTiers!: StoreAction<void>

  @promotions.Getter(GET_PROMOTION_QUALITY_TIERS_GETTER)
  promotionQualityTiers!: PromotionQualityTiersListVM

  @authentication.Getter(IS_STORE_ADMIN_LOADED_GETTER)
  isStoreAdminLoaded!: boolean

  get shouldLoadPromoQualityTiers() {
    return (
      arePromotionVisibilityRulesEnabled(this.toggles) &&
      !this.promotionQualityTiers?.hasTiers
    )
  }

  mounted() {
    this.displayState(DISPLAY_PROMOTIONS)
    this.handlePromotionsVisibility()
  }

  beforeDestroy() {
    clearTimeout(this.promotionsViewTimeout)
  }

  @Watch('isStoreAdminLoaded')
  handleStoreAdminLoaded() {
    clearTimeout(this.promotionsViewTimeout)
    this.enablePromotions()
    this.redirectHomeIfPromotionsDisabled()
  }

  handlePromotionsVisibility() {
    this.enablePromotions()
    this.promotionsViewTimeout = setTimeout(
      this.redirectHomeIfPromotionsDisabled,
      ONE_SECOND_IN_MILLIS
    )
  }

  enablePromotions() {
    if (this.isStoreAdminLoaded && arePromotionsEnabled(this.toggles)) {
      this.initialisePromotionsDependencies()
      this.promotionsContainerEnabled = true
    }
  }

  initialisePromotionsDependencies() {
    registerPromoTypesService.setFeatureToggles(this.toggles)
    this.loadPromotionQualityTiers()
  }

  loadPromotionQualityTiers() {
    if (this.shouldLoadPromoQualityTiers) {
      this.getPromotionQualityTiers()
    }
  }

  redirectHomeIfPromotionsDisabled() {
    if (!this.promotionsContainerEnabled) {
      this.$router.push('/')
    }
  }
}
