
import Vue from 'vue'
import Component from 'vue-class-component'
import { PromotionType } from '../../api/dtos'
import { registerPromoTypesService } from '../../services/register-promo-types'
import CreatePromotionDialogButton from './CreatePromotionDialogButton.vue'
import CreatePromotionButtonLink from './CreatePromotionButtonLink.vue'

@Component({
  name: 'CreatePromotionButtonProvider',
  components: { CreatePromotionDialogButton, CreatePromotionButtonLink }
})
export default class CreatePromotionButtonProvider extends Vue {
  get discountPromotionsEnabled() {
    return registerPromoTypesService.isPromotionTypeEnabled(
      PromotionType.PERCENTAGE_DISCOUNT
    )
  }

  get createPromotionComponent() {
    return this.discountPromotionsEnabled
      ? CreatePromotionDialogButton
      : CreatePromotionButtonLink
  }
}
