
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Component from 'vue-class-component'
import Popper, { PopperProps } from 'vue-popperjs'
import merge from 'lodash/merge'

const DEFAULT_OPTIONS = {
  placement: 'bottom-start',
  modifiers: { offset: { offset: '0,8px' } }
}

@Component({
  name: 'GlovoInnerTooltip',
  components: { Popper },
  extends: Popper
})
export default class GlovoInnerTooltip extends Vue<PopperProps> {
  @Prop({ default: null }) triggerRef!: Vue
  @Prop({ default: 'clickToToggle' }) trigger!: string

  get mergedOptions() {
    return merge({}, DEFAULT_OPTIONS, this.options)
  }

  get isPopperVisible() {
    return this.referenceElement || this.$slots.reference
  }

  get referenceElement() {
    if (!this.triggerRef) {
      return null
    }

    return this.triggerRef.$el || this.triggerRef
  }
}
