
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import Icon from 'common/Icon.vue'
import { LayoutDirection } from '../../types'

const CSS_CLASSES: Record<LayoutDirection, string> = {
  horizontal: 'banner--horizontal',
  vertical: 'banner--vertical'
}

@Component({
  name: 'GlovoBanner',
  components: { Icon }
})
export default class GlovoBanner extends Vue {
  @Prop({ default: 'horizontal' }) layout!: LayoutDirection
  @Prop({ default: false }) dismissible!: boolean

  get bannerClass() {
    return CSS_CLASSES[this.layout]
  }

  async handleCloseBannerClick(e: Event) {
    this.$emit('close', e)
  }
}
