
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import debounce from 'lodash/debounce'
import { FOUR_HUNDRED_MILLIS } from '@/modules/core/helpers'
import { StoreAction } from '@/modules/store/types/store'
import {
  PromotionId,
  PromotionStatus,
  PromotionStatusName
} from '../../../api/dtos/promotions-dtos'
import Icon from 'common/Icon.vue'
import { GlovoTooltip } from 'common/ui/tooltip'
import { promotions } from '../../../store/namespace'
import {
  UPDATE_PROMOTION,
  UpdatePromotionFailurePayload
} from '../../../store/action-types'
import { UpdatePromotionPayload } from '../../../api/promotions'
import { UPDATE_PROMOTION_ERROR_GETTER } from '../../../store/getters'
import { Promotion } from '../../../models'

@Component({
  name: 'PromotionListStatusDisplay',
  components: {
    Icon,
    GlovoTooltip
  }
})
export default class PromotionListStatusDisplay extends Vue {
  private tooltipTriggerRef: unknown = null
  private toggleValue: boolean | null = null
  public handleToggleChangeDebounced = debounce(
    this.handleToggleChange,
    FOUR_HUNDRED_MILLIS
  )

  @Prop({ required: true }) promotionId!: PromotionId
  @Prop({ required: true }) promotionName!: Promotion['name']
  @Prop({ required: true }) promotionStatus!: PromotionStatus

  @promotions.Action(UPDATE_PROMOTION) updatePromotion!: StoreAction<
    UpdatePromotionPayload
  >

  @promotions.Getter(UPDATE_PROMOTION_ERROR_GETTER)
  updatePromotionApiError!: UpdatePromotionFailurePayload

  mounted() {
    this.tooltipTriggerRef = this.$refs.iIcon
    this.toggleValue = this.toggleEnabled
  }

  get toggleEnabled() {
    return this.promotionStatus.name === PromotionStatusName.ENABLED
  }

  get shouldDisplayToggle() {
    return this.promotionStatus.name !== PromotionStatusName.COMPLETED
  }

  get toolTipText() {
    return this.promotionStatus.description
  }

  async handleToggleChange() {
    this.toggleValue = !this.toggleEnabled
    this.showToggleUpdateToast()

    try {
      await this.updatePromotion({
        id: this.promotionId,
        enabled: this.toggleValue
      })
    } catch (err) {
      this.handleUpdatePromotionError(err)
    }
  }

  handleUpdatePromotionError(errorMessage: string) {
    this.$emit('promotion-update-error', errorMessage)
  }

  showToggleUpdateToast() {
    const message = this.$t(
      this.toggleValue
        ? 'promotions.status.enabled.message'
        : 'promotions.status.disabled.message',
      { promotionName: this.promotionName }
    ) as string

    this.$toast.show({ message })
  }
}
