import cloneDeep from 'lodash/cloneDeep'
import { FeatureToggleMap } from '@/modules/feature-toggles/store/state'
import { PromotionType } from '../../api/dtos'
import { CreatePromotionFieldId } from '../../types'
import {
  getEnabledPromotionTypes,
  areDiscountPromotionsWithProductsEnabled,
  isOneStorePerPromotionEnabled
} from '../../helpers'

interface PromotionTypeAssets {
  iconName: string
  titleKey: string
  messageKey: string
}

export interface PromotionRegistryConfig {
  promotionType: PromotionType
  createPromotionFieldIds: CreatePromotionFieldId[]
  assets: PromotionTypeAssets
}

export type PromoTypesRegistry = Record<PromotionType, PromotionRegistryConfig>

class RegisterPromoTypesService {
  private registry: PromoTypesRegistry = {} as PromoTypesRegistry
  private toggles: FeatureToggleMap = {}

  setFeatureToggles(toggles: FeatureToggleMap) {
    this.toggles = toggles
    this.registry = {} as PromoTypesRegistry

    getEnabledPromotionTypes(this.toggles)?.forEach(this.registerPromotionType)
  }

  get orderedPromotionsConfigs(): PromotionRegistryConfig[] {
    return ORDERED_PROMOTION_TYPES.filter(this.isPromotionTypeEnabled).map(
      this.getPromotionConfig
    )
  }

  isPromotionTypeEnabled = (promotionType: PromotionType): boolean => {
    return !!this.registry[promotionType]
  }

  getPromotionConfig = (
    promotionType: PromotionType
  ): PromotionRegistryConfig => {
    const config = this.registry[promotionType] || null
    if (!config) {
      throw new Error(`promotionType ${promotionType} is not supported`)
    }
    return config
  }

  private registerPromotionType = (promotionType: PromotionType) => {
    const config = cloneDeep(PROMOTIONS_REGISTRY[promotionType])
    if (!config) {
      throw new Error(
        `there is no configuration for promotion type ${promotionType}`
      )
    }

    if (
      promotionType === PromotionType.PERCENTAGE_DISCOUNT &&
      !areDiscountPromotionsWithProductsEnabled(this.toggles)
    ) {
      this.removeFieldWithId(config, 'CHOOSE_PROMOTION_PRODUCTS')
    }

    if (isOneStorePerPromotionEnabled(this.toggles)) {
      this.replaceField(
        config,
        'CHOOSE_PROMOTION_STORES',
        'CHOOSE_PROMOTION_STORE'
      )
    }

    this.registry[promotionType] = config
  }

  private removeFieldWithId = (
    config: PromotionRegistryConfig,
    fieldId: CreatePromotionFieldId
  ) => {
    const fieldIndex = config.createPromotionFieldIds.indexOf(fieldId)
    if (fieldIndex > -1) {
      config.createPromotionFieldIds.splice(fieldIndex, 1)
    }
  }

  private replaceField = (
    config: PromotionRegistryConfig,
    fieldIdToRemove: CreatePromotionFieldId,
    fieldIdToInsert: CreatePromotionFieldId
  ) => {
    const fieldIndex = config.createPromotionFieldIds.indexOf(fieldIdToRemove)

    if (fieldIndex === -1) {
      throw new Error(
        `fieldId ${fieldIdToRemove} does not exist on promotion type`
      )
    }

    config.createPromotionFieldIds[fieldIndex] = fieldIdToInsert
  }
}

const ORDERED_PROMOTION_TYPES = Object.freeze<PromotionType[]>([
  PromotionType.FREE_DELIVERY,
  PromotionType.PERCENTAGE_DISCOUNT
])

const PROMOTIONS_REGISTRY = Object.freeze<Partial<PromoTypesRegistry>>({
  FREE_DELIVERY: {
    promotionType: PromotionType.FREE_DELIVERY,
    createPromotionFieldIds: [
      'CHOOSE_PROMOTION_NAME',
      'CHOOSE_PROMOTION_DATES',
      'CHOOSE_PROMOTION_STORES',
      'CHOOSE_PROMOTION_BUDGET',
      'CHOOSE_PROMOTION_ENABLED',
      'PROMOTION_TERMS'
    ],
    assets: {
      iconName: 'free-delivery-bike.svg',
      titleKey: 'promotions.free_delivery.text',
      messageKey: 'promotions.free_delivery_campaign.description'
    }
  },
  PERCENTAGE_DISCOUNT: {
    promotionType: PromotionType.PERCENTAGE_DISCOUNT,
    createPromotionFieldIds: [
      'CHOOSE_PROMOTION_NAME',
      'CHOOSE_PROMOTION_DATES',
      'CHOOSE_PROMOTION_STORES',
      'CHOOSE_PROMOTION_PRODUCTS',
      'CHOOSE_PROMOTION_PERCENTAGE',
      'CHOOSE_PROMOTION_BUDGET',
      'CHOOSE_PROMOTION_ENABLED',
      'PROMOTION_TERMS'
    ],
    assets: {
      iconName: 'percentage-discount.svg',
      titleKey: 'promotions.percentage_discount.text',
      messageKey: 'promotions.percentage_discount_campaign.description'
    }
  }
})

const registerPromoTypesService = new RegisterPromoTypesService()
export default registerPromoTypesService
