
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { PromotionInfo } from '../../models'
import PromotionDetailsDisplay from '../promotion-details-display/PromotionDetailsDisplay.vue'
import { PromotionDetails } from '../promotion-details-display/PromotionDetailsDisplay.types'

@Component({
  name: 'PromotionStoreDetails',
  components: { PromotionDetailsDisplay }
})
export default class PromotionStoreDetails extends Vue {
  @Prop({ required: true }) promotionInfo!: PromotionInfo

  get detailsLinkText() {
    const { totalStoreAddressesCount, storesInPromotion } = this.promotionInfo
    if (totalStoreAddressesCount === 1) {
      return storesInPromotion[0].storeAddresses[0]
    }
    return this.$t('promotions.addresses.text', {
      addressesCount: totalStoreAddressesCount
    }) as string
  }

  get titleForDetailsModal() {
    return this.$t('promotions.store_addresses.text', {
      addressesCount: this.promotionInfo.totalStoreAddressesCount
    }) as string
  }

  get detailsDisplayInfo(): PromotionDetails {
    const { totalStoreAddressesCount, storesInPromotion } = this.promotionInfo
    return {
      totalCount: totalStoreAddressesCount,
      items: storesInPromotion.map(store => ({
        name: store.storeName,
        subItems: store.storeAddresses
      }))
    }
  }
}
