
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import CreatePromotionButton from './CreatePromotionButton.vue'
import { CREATE_PROMO_ROUTES } from '../../promotions-routes'
import { PromotionType } from '../../api/dtos'

@Component({
  name: 'CreatePromotionButtonLink',
  components: { CreatePromotionButton }
})
export default class CreatePromotionButtonLink extends Vue {
  @Prop({ default: PromotionType.FREE_DELIVERY }) promotionType!: PromotionType

  get createPromoRouteName() {
    return CREATE_PROMO_ROUTES[this.promotionType]
  }
}
